.view-name-container{
    height: 100vh;
    width: 100vw;
    background-color: transparent;
    align-items: center;
    display: flex;
    justify-content: center;
}

.background{
    height: 100%;
    width: 100%;
    position: absolute;
    background-color: #11bbff;
    z-index: -20;
}

.clouds{
    height: 100%;
    width: 100%;
    position: absolute;
    z-index: -10;
    overflow: hidden;
}

#cloud1{
    position: absolute;
}
#cloud2{
    position: absolute;
    z-index: -12;
    opacity: 60%;
}
#cloud3{
    position: absolute;
    z-index: -14;
    opacity: 20%;
}
#cloud4{
    position: absolute;
}
#cloud5{
    position: absolute;
    z-index: -12;
    opacity: 60%;
}
#cloud6{
    position: absolute;
    z-index: -14;
    opacity: 20%;
}
#tree1{
    position: absolute;
    z-index: -11;
}
#tree2{
    position: absolute;
    z-index: -13;
    scale: 50%;
}
#tree3{
    position: absolute;
    z-index: -15;
    scale: 50%;
}
#tree4{
    position: absolute;
    z-index: -12;
    scale: 50%;
}
#tree5{
    position: absolute;
    z-index: -14;
    scale: 50%;
}
#tree6{
    position: absolute;
    z-index: -16;
    scale: 50%;
}

.tree{
    transform: scale(0.2);
}
.cloud{
    transform: scale(0.5);
}

@media screen and (min-width: 500px) {
    .tree{
        transform: scale(0.3);
    }
    .cloud{
        transform: scale(0.8);
    }
}


@media screen and (min-width: 1100px) {
    .tree{
        transform: scale(0.4);
    }
    .cloud{
        transform: scale(1);
    }
}
